"use client";
import { useRouter } from "next/navigation";
import { TextField, Button, Paper } from "@mui/material";
import { useState } from "react";

export default function AddUser() {
  const router = useRouter();
  const [form, setForm] = useState({
    username: "",
    email: "",
    password_hash: "",
    first_name: "",
    last_name: "",
    date_of_birth: "",
    is_active: false,
  });

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  return (
    <Paper sx={{ p: 3, maxWidth: 500, margin: "auto" }}>
      <h2>Add User</h2>

      <TextField size="small" label="Username" name="username" fullWidth margin="dense" onChange={handleChange} />
      <TextField size="small" label="Email" name="email" type="email" fullWidth margin="dense" onChange={handleChange} />
      <TextField size="small" label="Password" name="password_hash" type="password" fullWidth margin="dense" onChange={handleChange} />
      <TextField size="small" label="First Name" name="first_name" fullWidth margin="dense" onChange={handleChange} />
      <TextField size="small" label="Last Name" name="last_name" fullWidth margin="dense" onChange={handleChange} />
      <TextField size="small" label="Date of Birth" name="date_of_birth" type="date" fullWidth margin="dense" InputLabelProps={{ shrink: true }} onChange={handleChange} />

      <div style={{ marginTop: "16px", display: "flex", gap: "10px" }}>
        <Button className="btn-primary">Save</Button>
        <Button className="btn-secondary" onClick={() => router.push("/users")}>Back</Button>
      </div>
    </Paper>
  );
}
